import { BLOG_DETAIL, BLOG_LIST, ADD_BLOG_COMMENT, BLOG_CATEGORY_LIST } from "@/api/api";
import featuredJobs from "@/components/featuredJobs";
import latestIndustryNews from "@/components/latestIndustryNews";
export default {
  name: "detail",

  data() {
    return {
      showCommentLength: 4,
      commentTotalNum: 0,
      showSocialShareExpandStatus: true,
      locationUrl: '',
      showLoadingStatus: false,
      detailData: {},
      basicForm: {
        blog_id: undefined,
        content: '',
        name: '',
        email: '',
        image_url: '',
        token: localStorage.getItem('token')
      },
      basicRules: {
        content: [{
          required: true,
          message: 'Please input',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          message: 'Please input',
          trigger: 'blur'
        }],
        email: [{
          required: true,
          message: 'Please input',
          trigger: 'blur'
        }]
      },
      blogCategoryData: []
    };
  },

  components: {
    featuredJobs,
    latestIndustryNews
  },

  setup() {},

  created() {
    let id = this.$route.query.id;
    this.getBlogDetail(id);
    this.locationUrl = window.location.href;
  },

  mounted() {
    this.getBlogCategoryList();
  },

  methods: {
    filterBlog(cateId) {
      this.$router.push({
        path: '/blog/list',
        query: {
          cate_id: cateId
        }
      });
    },

    getBlogCategoryList() {
      let params = {};
      BLOG_CATEGORY_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          let message = res.message; // console.log(a)

          this.blogCategoryData = message.filter(item => item.is_delete == 0);
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    showMoreComment() {
      this.showCommentLength += 4;
    },

    showCloseComment() {
      this.showCommentLength -= 4;

      if (this.showCommentLength <= 0) {
        this.showCommentLength = 0;
      }
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$loading({
            text: 'Loading'
          });
          this.basicForm.blog_id = this.$route.query.id;
          let params = Object.assign({}, this.basicForm);
          ADD_BLOG_COMMENT(params).then(res => {
            console.log(res);

            if (res.code == 200) {
              this.$loading().close();
              this.getBlogDetail(this.$route.query.id);
            }
          }).catch(err => {
            console.log(err);

            if (err.msg) {
              this.$message.error(err.msg);
            }

            if (err.message) {
              this.$message.error(err.message);
            }
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    getBlogDetail(id) {
      let params = {
        id: id
      };
      BLOG_DETAIL(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.detailData = res.message;
          let commentData = res.message.comment;

          if (commentData) {
            this.commentTotalNum = commentData.length;
          }
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    getBlogList() {
      let params = {
        page: 1,
        limit: 5
      };
      BLOG_LIST(params).then(res => {
        console.log(res);
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    }

  }
};